.header {
  z-index: 9;
  background-color: transparent;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  color: $white;
  border-top: 1px solid rgba(255, 255, 255, 0.2);

  @include md {
    bottom: auto;
    top: 0;
    padding: 0;
    pointer-events: none;
    border: none; }

  &.is-sticky,
  &.visible {
    background-color: rgba(0, 0, 0, 0.8);

    @include md {
      background-color: transparent; } }

  &__logo {
    display: none; }

  &__nav {
    grid-column: 2;
    @include md {
      width: 100%;
      margin: auto 0;
      padding: 20px 0; } }

  &__btn {
    display: flex;
    justify-content: flex-end;
    @include md {
      order: 4;
      width: 100%;
      justify-content: center; }
    .btn {
      width: 100%;
      max-width: 180px;
      @extend .btn--border;
      @include tablet {
        max-width: 140px; }
      @include md {
        max-width: 200px; }
      &:hover {
        background: $themeColor; } } }

  &__alt-btn {
    display: none;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    pointer-events: auto;
    transition: all ease 0.1s;

    &.is-visible {
      @include md {
        width: 160px;
        display: block; } }

    .btn {
      width: 100%;
      height: 48px;
      border-radius: 0;
      padding: 0;
      font-size: 14px;
      background: $themeColor; } }

  &__socials {
    display: flex;
    align-items: center;

    @include md {
      order: 3; } }

  &__inner {
    position: relative;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    max-width: 1480px;
    padding: 16px 20px;
    align-items: center;
    gap: 20px;
    min-height: 86px;
    margin: 0 auto;
    @include md {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      height: 100svh;

      padding: 20px;
      overflow: hidden;
      overflow-y: auto;
      gap: 30px;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      background-color: rgba(0,0,0,0.7);
      z-index: 3; }

    &.is-open {
      @include md {
        display: flex;
        pointer-events: auto; } } }

  &__burger {
    display: none;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 4;
    @include md {
      display: inline-block;
      pointer-events: auto; } } }

.nav {
  display: flex;
  @include md {
    display: block; }
  &__link {
    text-decoration: none;
    color: $white;
    transition: 0.1s;
    font-size: 12px;
    @include bold;
    height: 50px;
    display: flex;
    padding: 0 13px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    border: 1px solid transparent;
    @include laptop {
      font-size: 12px;
      padding-left: 10px;
      padding-right: 10px; }
    @include tablet {
      font-size: 11px;
      padding-left: 8px;
      padding-right: 8px; }
    @include md {
      color: $white; }

    &:hover {
      color: $white;
      background: rgba(0, 0, 0, 0.1);
      border: 1px solid rgba(255, 255, 255, 0.2);

      @include md {
        background: rgba(0, 0, 0, 0.5); } }

    @include md {
      font-size: 14px;
      display: flex;
      text-align: center;
      padding: 10px 20px;
      font-weight: 500; }

    .no-touch &:hover {
      color: $white;

      @include md {
        color: $white; } } } }

.burger {
  width: 60px;
  height: 60px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include sm {
    width: 50px;
    height: 50px; }

  span {
    transition: 0.3s;
    display: block;
    height: 4px;
    width: 36px;
    background-color: #ffffff;
    border-radius: 5px;
    &:not(:last-child) {
      margin-bottom: 6px; } }
  &.is-active {
    span {
      &:nth-child(1) {
        transform: translate(0, 10px) rotate(45deg); }
      &:nth-child(2) {
        transform: translateX(0);
        opacity: 0; }
      &:nth-child(3) {
        transform: translate(0, -10px) rotate(-45deg); } } } }
