.s-free-ticket {
  padding: clamp(50px, 5vw, 100px) 0;
  border-top: 1px solid $gray;
  border-bottom: 1px solid $gray;

  .container {
    max-width: 1320px; }

  .section {
    &__title {
      @include black;
      font-stretch: 120%;
      line-height: 1.2;
      font-size: clamp(24px, 6vw, 64px);
      text-transform: uppercase;
      margin: 0 auto 26px;
      max-width: 900px;
      position: relative;
      z-index: 1;
      color: $black; } } }

.free-ticket {

  &__content {
    display: flex;
    max-width: 1062px;
    margin: 0 auto;
    gap: 20px 76px;
    justify-content: space-between;

    @include md {
      gap: 20px 40px;
      flex-direction: column-reverse; } }

  &__text {
    display: flex;
    flex-direction: column;
    flex: 0 1 520px;
    justify-content: center;

    @include md {
      flex: 1 1 auto;
      text-align: center; }

    .btn {
      margin-top: 30px;
      @extend .btn;
      background: $black;
      display: flex;
      width: max-content;
      margin-right: auto;

      @include md {
        margin: 30px auto 0; }
      @include sm {
        font-size: 12px; }

      &:hover {
        background: $themeColor; } } }

  &__descr {
    font-family: $roboto;
    font-size: clamp(14px, 2vw, 24px);
    line-height: 1.34;
    font-weight: 500;
    text-transform: uppercase;

    em {
      color: $themeColor;
      font-style: normal; }

    p {
      &:not(:first-child) {
        margin: 24px 0 0; } } }

  &__img {
    flex: 0 0 413px;

    @include md {
      flex: 0 0 auto;
      margin: 0 auto; }

    @include xs {
      max-width: 232px; }

    img {
      display: block;
      width: 100%;
      height: auto; } }

  &__form {
    display: flex;
    max-width: 974px;
    margin: 0 auto;

    @include md {
      flex-direction: column;
      max-width: 310px; } }

  &__input {
    @include regular;
    min-height: 52px;
    line-height: 1.24;
    flex: 1 1 auto;
    background: $white;
    padding: 14px 20px;
    font-size: 14px;
    display: block;

    &::placeholder {
      color: $black; }

    &:not(:first-child) {
      margin: 0 0 0 3px; }

    &:first-of-type {
      border-radius: 26px 0 0 26px; }

    &:last-of-type {
      border-radius: 0 26px 26px 0; }

    @include md {
      border-radius: 26px;

      &:not(:first-child) {
        margin: 10px 0 0 0; }

      &:first-of-type,
      &:last-of-type {
        border-radius: 26px; } } }

  &__submit {
    flex: 0 0 180px;
    margin: 0 0 0 12px;
    min-height: 52px;
    background: $black;

    &:after,
    &:before {
      display: none; }

    @include md {
      margin: 30px auto 0;
      flex: 0 0 auto;
      mix-width: 180px; } } }

.badge-img {
  position: relative;
  width: 413px;
  @include sm {
    width: 234px; }
  &:before {
    content: '';
    position: absolute;
    width: 120px;
    height: 100px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: linear-gradient(90deg, #EE6431 2.54%, #6565FF 21.28%, #A7A7FF 33.52%, #6DA4BF 44.52%, #FF9022 68.61%, #FFD0A8 80.79%, #FDAC5C 92.51%), #000000;
    opacity: 0.78;
    box-shadow: 0px 10px 250px #E38171;

    @include sm {
      width: 90px;
      height: 80px; } }
  &:after {
    content: '';
    position: absolute;
    right: 144px;
    bottom: calc(100% - 30px);
    height: 150px;
    width: 44px;
    background: linear-gradient(0deg, #F2F2F2 0%, rgba(255,255,255,0) 100%);
    z-index: -1;
    transform: skew(-32deg);
    border-radius: 0 0 2px 2px;
    @include md {
      height: 100px;
      right: 158px; }
    @include sm {
      width: 24px;
      height: 64px;
      right: 94px;
      border-radius: 0 0 1px 1px; } }

  &__card {
    position: relative;
    padding: 10px 30px 35px;
    border-radius: 10px;
    background: rgba(240, 240, 240, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.3);
    box-shadow: 0px 0 40px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(40px);

    @include sm {
      border-radius: 10px;
      padding: 10px 10px 20px; } }

  &__body {
    display: flex;
    align-items: center;
    padding: 0 8%; }

  &__img {
    margin: 0 20px 0 0;
    width: 95px;
    height: 95px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.3);
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: center;

    @include sm {
      width: 54px;
      height: 54px;
      margin: 0 12px 0 0; }

    .icon-face {
      width: 46px;
      height: 58px;
      fill: $black;

      @include sm {
        width: 26px;
        height: 34px; } } }

  &__desc {

    div:first-child {
      @include black;
      font-size: 32px;
      text-transform: uppercase;
      line-height: 1;
      font-stretch: 120%;
      margin: 0 0 2px;

      @include sm {
        font-size: 18px; } }

    div:last-child {
      font-size: 12px;
      margin: 8px 0 0;
      line-height: 0.9;

      @include sm {
        font-size: 8px;
        margin: 5px 0 0; } } }

  &__barcode {
    position: relative;
    width: 106px;
    height: 20px;
    background: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='106.5px' height='20px' viewBox='0 0 106.5 20' style='enable-background:new 0 0 106.5 20;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill:%23000000;%7D%0A%3C/style%3E%3Cg%3E%3Cpath class='st0' d='M0,0h20v6.7H0L0,0z'/%3E%3Cpath class='st0' d='M0,13.3h6.7V20H0L0,13.3z'/%3E%3Cpath class='st0' d='M6.7,6.7h6.7v6.7l-6.7,0L6.7,6.7z'/%3E%3Cpath class='st0' d='M13.3,13.3l6.7,0V20h-6.7L13.3,13.3z'/%3E%3C/g%3E%3Cg%3E%3Cpath class='st0' d='M27,20V0l3.5,0v20H27z'/%3E%3Cpath class='st0' d='M33.9,20V0l1.2,0v20H33.9z'/%3E%3Cpath class='st0' d='M36.2,20V0l2.3,0v20H36.2z'/%3E%3Cpath class='st0' d='M40.8,20V0L42,0v20H40.8z'/%3E%3Cpath class='st0' d='M43.1,20V0l1.2,0v20H43.1z'/%3E%3Cpath class='st0' d='M45.4,20V0l1.2,0v20H45.4z'/%3E%3Cpath class='st0' d='M47.7,20V0l3.5,0v20H47.7z'/%3E%3Cpath class='st0' d='M54.7,20V0l1.2,0v20H54.7z'/%3E%3Cpath class='st0' d='M57,20V0l2.3,0v20H57z'/%3E%3Cpath class='st0' d='M61.6,20V0l1.2,0v20H61.6z'/%3E%3Cpath class='st0' d='M63.9,20V0L65,0v20H63.9z'/%3E%3Cpath class='st0' d='M71.9,20V0l1.2,0v20H71.9z'/%3E%3Cpath class='st0' d='M74.2,20V0l3.5,0v20H74.2z'/%3E%3Cpath class='st0' d='M81.2,20V0l1.2,0v20H81.2z'/%3E%3Cpath class='st0' d='M83.5,20V0l2.3,0v20H83.5z'/%3E%3Cpath class='st0' d='M88.1,20V0l1.2,0v20H88.1z'/%3E%3Cpath class='st0' d='M90.4,20V0l1.2,0v20H90.4z'/%3E%3Cpath class='st0' d='M92.7,20V0l1.2,0v20H92.7z'/%3E%3Cpath class='st0' d='M95,20V0l3.5,0v20H95z'/%3E%3Cpath class='st0' d='M101.9,20V0l1.2,0v20H101.9z'/%3E%3Cpath class='st0' d='M104.2,20V0l2.3,0v20H104.2z'/%3E%3C/g%3E%3C/svg%3E") center center no-repeat;

    @include sm {
      width: 62px;
      height: 12px; } }

  &__stripe {
    position: absolute;
    width: 60px;
    height: 18px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    left: 50%;
    top: 24px;
    transform: translateX(-50%);
    border-radius: 12px;
    background: $gray;

    @include sm {
      width: 40px;
      height: 12px;
      top: 12px; }

    &:before {
      content: '';
      position: absolute;
      left: -39px;
      bottom: calc(100% - 2px);
      height: 150px;
      width: 44px;
      background: linear-gradient(0deg, #F2F2F2 0%, #F2F2F2 50%, rgba(255,255,255,0) 100%);
      transform: skew(32deg);
      border-radius: 0 0 2px 2px;
      @include md {
        height: 100px;
        left: -24px; }

      @include sm {
        width: 24px;
        height: 64px;
        left: -12px;
        border-radius: 0 0 1px 1px; } } }

  &__head {
    font-size: 18px;
    line-height: 1;
    font-weight: 400;
    padding: 15px 0 25px;
    margin-bottom: 25px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);

    @include sm {
      padding-bottom: 15px;
      padding-top: 5px;
      margin-bottom: 20px;
      font-size: 12px; }

    .icon {
      width: 10px;
      height: 10px;
      fill: $white;

      @include sm {
        width: 8px;
        height: 8px; } } } }
