.s-party {
  padding: clamp(50px, 5vw, 100px) 0;

  .section__title {
    text-transform: uppercase;
    line-height: 1;
    @include black;
    color: $black;
    font-size: clamp(29px, 8vw, 84px);
    font-stretch: 120%; }

  &__sponsored {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px 20px;
    margin-top: calc(clamp(10px, 4vw, 60px) * -1);
    margin-bottom: clamp(30px, 4vw, 70px);
    span {
      white-space: nowrap;
      font-size: 16px;
      color: #6C6C6C;
      @include medium;
      @include sm {
        font-size: 14px; } }
    a {
      text-decoration: none;
      img {
        max-width: 160px;
        max-height: 60px;
        @include sm {
          max-width: 120px;
          max-height: 40px; } } } } }
.party {
  margin-top: clamp(38px, 8vw, 100px);
  &__item {
    display: flex;
    justify-content: space-between;

    @include sm {
      flex-direction: column-reverse; }

    &:nth-child(even) {
      flex-direction: row-reverse;

      @include sm {
        flex-direction: column-reverse; } }

    &:not(:first-child) {
      margin-top: 100px;

      @include sm {
        margin-top: 60px; } }

    @include md {
      gap: 50px; }

    @include sm {
      gap: clamp(20px, 4vw, 30px); } }

  &__text {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    max-width: 630px;
    min-width: 320px;
    padding: 0 clamp(40px, 7vw, 100px);
    justify-content: center;

    @include md {
      padding: 0; }

    @include sm {
      text-align: center;
      min-width: 0; } }

  &__images {
    flex: 1 1;
    max-width: 490px;
    min-width: 320px;
    gap: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @include sm {
      margin: 0 auto;
      max-width: 380px;
      min-width: 0;
      gap: 5px; } }

  &__img {

    &.single {
      grid-area: 1 / 1 / 3 / 3; }

    &.fill-height {
      grid-area: 1 / 1 / 3 / 2; }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 10px;

      @include sm {
        border-radius: 6px; } } }

  &__title {
    @include black;
    text-transform: uppercase;
    font-size: clamp(22px, 5vw, 46px);
    line-height: 1;
    color: $black;
    font-stretch: 120%; }

  &__desc {
    @include medium;
    font-size: clamp(14px, 2.6vw, 18px);
    line-height: 1.68;
    margin: 18px 0 0;
    color: $gray3; } }
