
.hero {
  position: relative;
  min-height: 100vh;
  padding: 30px 0 80px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #1e2220;
  z-index: 1;
  pointer-events: none;
  color: $white;

  &:after {
    content: '';
    @include coverdiv;
    z-index: 1;
    background-color: #000;
    opacity: 0.35; }

  @include tablet {
    padding: 45px 0 105px; }
  @include md {
    padding: 60px 0 80px; }
  @include sm {
    padding: 20px 0 60px; }
  @include r(380) {
    @include rhmin(600) {
      padding: 20px 0 40px!important;
      .hero__logo, .h-list {
        margin-bottom: 25px; } } }
  &__video {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    // top: 50%
    // left: 50%
    // transform: translate(-50%, -50%)
    // min-width: 100%
    // min-height: 100%
    // width: auto
    // height: auto
    z-index: -1;
    filter: brightness(0.7);
    object-fit: cover;

    @include sm {
      display: none; } }

  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -1;
    background: url(../img/hero.jpg) center center no-repeat;
    background-size: cover;
    filter: brightness(0.7); }

  &__inner {
    max-width: 1600px;
    //margin: 0 auto
    padding: 0 40px;
    z-index: 2;
    pointer-events: none;
    @include r(1460) {
      padding: 0 40px; }
    @include r(1279) {
      padding: 0 40px; }
    @include sm {
      padding: 0 20px; }
    @include xs {
      padding: 0 15px; } }

  &__switch {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    pointer-events: auto;
    margin-bottom: 20px;

    &-label {
      color: #fff;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 700;
      flex: 1;
      &:first-child {
        text-align: right; }
      &:last-child {
        text-align: left; }
      a {
        color: inherit;
        text-decoration: none;
        &:hover {
          text-decoration: underline; } } }

    &-handler {
      width: 58px;
      height: 32px;
      background: rgba(0, 0, 0, 0.6);
      border-radius: 30px;
      position: relative;
      flex: none;
      & > span {
        height: 26px;
        width: 26px;
        border-radius: 50%;
        position: absolute;
        left: 18px;
        top: 3px;
        transition: all 0.3s linear !important;
        background-color: rgba(#FF4D00, 0.8); }
      &:focus-visible {
        outline: 2px solid #fff; } }

    &._swipe {
      .hero__switch-handler {
        & > span {
          left: 3px; } } }
    &--reverse {
      .hero__switch-handler {
        & > span {
          left: 18px; } }
      &._swipe {
        .hero__switch-handler {
          & > span {
            left: 29px; } } } }

    @include md {
      gap: 10px;
      .hero__switch-label {
        font-size: 11px; } } }

  &__logo {
    width: 84px;
    margin: 0 auto 60px;

    @include tablet {
      margin: 0 auto 36px; }
    @include md {
      margin: 0 auto 30px; }
    @include xs {
      margin: 0 auto 26px;
      height: auto; }

    @media screen and (max-height: 768px) {
      margin: 0 auto 32px; } }

  &__suptitle {
    font-family: $roboto;
    font-size: 18px;
    line-height: 1;
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 24px;
    text-align: center;

    @include xs {
      margin: 0 -10px 20px; } }

  &__title {
    font-size: clamp(30px, 6vw, 76px);
    font-weight: 400;
    line-height: 1.2;
    text-align: center;
    font-stretch: 125%;
    max-width: 1200px;
    margin: 0 auto;
    margin-bottom: 38px;

    strong {
      @include black;
      font-stretch: 125%; }

    @media screen and (max-height: 768px) {
      margin-bottom: 20px; }

    @include tablet {
      margin-bottom: 20px;
      margin-left: -30px;
      margin-right: -30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 20px;
      margin-left: 0;
      margin-right: 0; }
    @include xs {
      margin: 0 auto 20px;
      max-width: calc(100% - 5px); } }
  &__btn {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px 20px;
    @include sm {
      gap: 10px 15px; }
    .btn {
      pointer-events: auto;
      @include xs {
        flex: 1;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 12px;
        min-width: 130px; } } }

  &__partner {
    position: absolute;
    right: 50%;
    transform: translateX(660px);
    top: 10px;
    font-size: 12px;
    line-height: 1.4;
    pointer-events: auto;
    z-index: 2;

    @include laptop {
      transform: translateX(0);
      right: 10px;
      top: 10px; }

    @include tablet {
      right: 20px; }

    @include md {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      right: 0;
      margin: 20px auto; } }

  &__partners {
    display: flex;
    margin-top: 4px;

    @include tablet {
      margin-top: 5px; } }

  &__partner-img {
    display: flex;
    align-items: center;
    justify-content: center;

    &:not(:first-child) {
      margin: 0 0 0 20px;

      @include sm {
        margin: 0 0 0 10px; } }

    img {
      height: 60px;
      width: auto; } }

  &__timer {
    position: absolute;
    padding: 0 4vw;
    right: 0;
    left: 0;
    bottom: 1vw;
    font-size: 15vw;
    font-weight: 600;
    color: $themeColor;
    opacity: .2;
    line-height: 1;
    white-space: nowrap;
    display: none;

    .live {
      text-align: center; } }
  &_inner {
    min-height: 50vh;
    padding: 60px 0 !important;
    @include sm {
      min-height: 34vh;
      padding: 50px 0 !important; }
    .hero__title {
      margin-bottom: 0; } }
  &_live {
    align-items: flex-start;
    height: 150px;
    min-height: 0;
    padding: 20px 0;

    @include tablet {
      padding: 20px 0; }
    @include md {
      padding: 20px 0;
      height: 120px; }
    @include sm {
      padding: 10px 0; }
    @include xs {
      height: auto;
      padding: 10px 0 !important; }

    .container {
      position: relative;
      z-index: 2;
      pointer-events: auto;
      width: 100%;
      height: 100%;
      padding: 0 80px;

      @include sm {
        padding: 0 20px; } }

    .hero {
      &__bg {
        z-index: -1;
        background: url(../img/screens-done-op.svg) center top no-repeat;
        background-size: cover; }

      &__inner {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: space-between;

        @include md {
          margin: 0; }

        @include xs {
          flex-direction: column-reverse;
          align-items: center; } }

      &__date {
        font-size: 18px;
        margin: auto 0;

        @include md {
          font-size: 14px; }

        @include xs {
          font-size: 12px;
          margin: 15px 0 10px; } }

      &__logo {
        width: 100px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        margin: 0;

        @include md {
          width: 80px;
          margin: 0px auto 0; }

        @include xs {
          position: relative;
          left: 0;
          top: 0;
          transform: translate(0, 0);
          width: 60px;
          margin: 0px auto 0; } }

      &__partner {
        position: relative;
        height: auto;
        margin: auto 0 auto auto;
        top: 0;
        right: 0;

        @include tablet {
          font-size: 10px; }

        @include md {
          margin: auto 0 auto;

          img {
            height: 45px; } }

        @include xs {
          display: none; }

        img {
          height: 65px; } }

      &__partners {
        @include md {
          margin: 0; } }

      &__partner-text {
        @include sm {
          display: none; } } } }

  &_attendees {
    min-height: 0;
    padding: 45px 0;
    background-size: cover;
    background-position: center center;

    .hero {

      &__inner {
        width: 100%; }

      &__logo {
        width: 200px;
        margin-bottom: 40px;

        @include md {
          width: 150px; }

        @include xs {
          width: 130px; } }

      &__title {
        text-align: center; }

      &__suptitle {
        margin-bottom: 10px; } } } }

.h-list {
  display: flex;
  justify-content: space-between;
  max-width: 720px;
  margin: 0 auto 40px;
  padding: 0;

  &_centered {
    justify-content: center; }

  &_min {
    max-width: 411px; }
  @include tablet {
    margin: 0 auto 40px; }
  @include md {
    max-width: 600px;
    margin-bottom: 30px;
    padding: 0; }
  @include xs {
    flex-wrap: wrap;
    justify-content: center;
    padding: 0;
    margin-bottom: 45px; }

  @media screen and (max-height: 768px) {
    margin: 0 auto 30px; }

  &__item {
    display: flex;
    flex-direction: column;

    @include md {
      flex-direction: column;
      text-align: center; }

    @include sm {
      width: 50%;
      align-items: center;
      text-align: center; }
    &:not(:last-child) {
      margin-right: 40px;
      @include sm {
        margin-right: 0; } }
    @include xs {
      margin-bottom: 9px; }
    &:nth-child(2n) {
      @include xs {
        margin-right: 0; } } }
  //+xs
  //    margin-right: 0
  //    //margin-bottom: 20px
  //    margin-bottom: 15px
  //&_sm
  //  @include xs
  //    max-width: 90px
  //&_lg
  //  @include xs
  //    width: auto
  //    max-width: unset

  &__value {
    font-size: clamp(30px, 6vw, 48px);
    margin: 0 20px 0 0;
    @include black;
    font-stretch: 120%;
    display: flex;
    align-items: center;
    @include md {
      margin: 0;
      justify-content: center; }
    @include sm {
      justify-content: flex-start; }
    @include xs {
      margin-bottom: 0;
      line-height: 1.1; }

    .icon-infinity {
      width: 71px;
      height: 60px;
      fill: #FFF; } }

  &__text {
    font-size: 12px;
    line-height: 1.4;
    padding: 5px 0;
    &-top {
      font-size: 20px;
      @include medium;
      text-transform: uppercase;
      @include tablet {
        font-size: 16px; }
      @include xs {
        font-size: 14px; } }
    @include xs {
      font-size: 10px; }
    @include tablet {
      padding: 9px 0; }
    @include md {
      padding: 0; } }
  &__icon {
    margin-top: 10px;
    margin-left: 8px;
    font-size: 30px;
    &_anim {
      animation: heart 2s infinite; }
    .icon {
      fill: $white; }
    @include sm {
      margin-top: 6px;
      .icon {
        width: 0.9em; } } } }

@keyframes heart {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

.logo {
  display: block;
  //height: 100%
  width: 100%;
  pointer-events: auto;
  img {
    //height: 100%
    width: 100%; } }

@media (prefers-reduced-motion: reduce) {
  .hero__video {
    display: none; } }
