.s-video-rooms {
  padding: clamp(50px, 5vw, 100px) 0;

  .container {
    max-width: 1260px; } }

.video-rooms {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  &__item {
    display: flex;
    gap: 30px;
    min-height: 100px;
    padding: 20px 0;
    position: relative;

    &:not(:first-child) {
      border-top: 3px solid $gray2; }

    @include sm {
      flex-direction: column;
      gap: 20px; }

    &[onclick] {
      cursor: pointer;
      &:hover {
        .video-rooms__title {
          text-decoration: underline; } } } }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-decoration: none; }
  &__img-wrap {
    display: flex;
    flex: 0 0 50px;
    height: 50px;
    margin: auto 0;
    width: 50px;
    justify-content: center; }

  &__img {
    width: 100%; }

  &__num {
    display: none; }

  &__title {
    text-transform: uppercase;
    display: flex;
    flex: 1 1 auto;
    font-weight: 900;
    line-height: 0.9;
    font-size: clamp(22px, 2.6vw, 28px);

    align-items: center; }

  &__users {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    flex: 0 0 50%;
    gap: 5px 10px;
    @include tablet {
      flex: 0 0 38%; }

    @include sm {
      flex-direction: row;
      flex: 1 1 auto;
      margin: 0 0;
      justify-content: flex-start; } }

  &__user {
    display: flex;
    align-items: center;
    width: calc(33.33% - 8px);
    @include tablet {
      width: calc(50% - 5px); }
    @include sm {
      width: auto;
      max-width: 140px; } }

  &__avatar {
    width: 30px;
    height: 30px;
    border-radius: 4px;
    margin: 0 12px 0 0; }

  &__name {
    font-size: 12px; }

  &__btn {
    display: none; } }
