.location {
	color: $black;
	padding: 160px 0 60px;

	@include tablet {
		padding-top: 120px; }

	@include md {
		padding-top: 100px; }

	@include sm {
		padding-top: 60px; }

	@include xs {
		padding-top: 40px; }

	.container {
		max-width: 1190px; }

	&__top {
		display: flex;
		position: relative;
		gap: 40px 126px;

		@include tablet {
			gap: 40px 80px; }

		@include sm {
			flex-direction: column; } }

	&__info {
		position: relative;
		z-index: 1;
		flex: 1 1; }

	&__map {
		position: relative;
		flex: 1 1;
		margin-top: -160px;

		@include tablet {
			margin-top: -120px;
			width: 600px; }

		@include md {
			margin-top: -100px;
			width: auto;
			max-width: 420px;
			margin: 0 auto; }

		@include sm {
			margin-top: 0; }

		.map {
			position: absolute;
			top: 0;
			left: 0;
			width: 705px;
			height: 100%;

			@include tablet {
				width: 540px; }

			@include md {
				width: 420px; }

			@include sm {
				width: auto;
				position: relative; }

			img {
				width: 100%;
				display: block; } } }

	&__title {
		font-weight: 400;
		line-height: 1.5;

		h3 {
			font-weight: 900;
			font-size: clamp(36px, 4vw, 64px);
			margin-bottom: 24px; }

		p {
			font-weight: 500;
			font-size: clamp(14px, 2vw, 18px);
			line-height: 1.8;
			color: $gray3; } }

	&__address {
		line-height: 1.5;
		margin: 30px 0 0;

		h3 {
			@include bold;
			font-size: clamp(26px, 2vw, 32px);
			font-stretch: 120%;
			line-height: 1.24; }

		p {
			font-size: 14px;
			@include medium; } }

	&__video {
		width: 100%;
		display: block;
		margin: 100px 0 0;
		border-radius: 30px;
		z-index: 1;
		position: relative;
		overflow: hidden;

		@include sm {
			margin: 20px 0 0;
			border-radius: 20px;

			.video {
				border-radius: 20px; } }

		@include xs {
			border-radius: 10px;

			.video {
				border-radius: 10px; } }


		.video {
			aspect-ratio: 1.68; } } }
