.footer {
  position: relative;
  background: $black;
  color: $white;

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    top: -204px;
    transform: translateX(-50%);
    margin: 0 0 0 724px;
    width: 964px;
    height: 1235px;
    background: url(../img/f-bg.svg) center center no-repeat;
    background-size: cover;
    pointer-events: none; }

  .container {
    max-width: 1180px;
    width: 100%;
    display: flex; }

  &__nav {
    @include md {
      margin-left: 0; }

    @include sm {
      width: 100%;
      margin: 0 0 24px; } }

  &__socials {
    margin: auto 0 0 auto;

    @include sm {
      margin: 0 0 30px;
      order: 0; }

    .social {
      display: flex;
      gap: 10px;
      margin: 0 auto;

      flex-wrap: wrap;

      &.social--theme {
        .social {
          &__item {
            margin: 0; } } }

      &__item {
        margin: 0; }

      &__link {
        width: clamp(46px, 10vw, 64px);
        height: clamp(46px, 10vw, 64px);
        border: 1px solid rgba(255, 255, 255, 0.2);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        transition: all ease 0.1s;

        &:hover, &:focus-visible {

          border-color: $themeColor;
          background: $themeColor;

          .icon {
            fill: $white; } }

        .icon {
          width: 32px;
          height: 32px;
          fill: $white;
          &-tiktok, &-instagram {
            width: 28px;
            height: 28px; }

          @include sm {
            width: 26px;
            height: 26px;
            &-tiktok, &-instagram {
              width: 22px;
              height: 22px; } } } } } }

  &__main {
    padding: clamp(50px, 5vw, 100px) 0;
    display: flex;
    width: 100%;
    flex-direction: column;
    @include laptop {
      padding: 80px 0 60px; }
    @include sm {
      padding: 50px 0 30px; } }

  &__title {
    font-size: clamp(32px, 5vw, 64px);
    font-weight: 800;
    margin-bottom: clamp(44px, 5vw, 64px);
    line-height: 1.34;
    text-transform: uppercase; }

  &__mid {
    display: flex;

    @include sm {
      flex-direction: column-reverse; } }

  &__basement {

    margin: 0 auto;
    background-position: top center;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding: 40px 0 120px;
    display: flex;

    @include md {
      padding: 25px 0; }

    .container {
      justify-content: space-between;
      gap: 20px;
      align-items: center;

      @include sm {
        flex-direction: column-reverse;
        align-items: flex-start; } } }

  &__tip {
    line-height: 1.42;
    font-size: 14px;
    color: #747474;

    a {
      display: inline-block;
      margin: 10px 0 0;
      font-weight: 600;
      color: $themeColor; }

    @include sm {
      font-size: 12px; } } }

.f-logo {
  color: $white;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 12px;

  &__label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 10px;
    margin-top: 4px;

    a {
      font-size: 18px;
      font-weight: 700;
      color: $white;
      text-decoration: none; } }

  &__icon {
    width: 36px;

    img {
      width: 100%; } } }

.f-nav {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 500;
  @include sm {
    font-size: 14px; }
  &__item {
    &:not(:first-child) {

      margin-top: 12px; } }

  &__link {
    color: $white;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } } }
