.techs {
  display: flex;
  flex-wrap: wrap;
  margin: 16px auto;
  align-items: stretch;
  justify-content: center;
  max-width: 960px;

  @include xs {
    margin: 32px -10px; }

  &__item {
    display: flex;
    justify-content: center;
    min-width: 85px;
    margin: 16px;
    flex-direction: column;
    padding: 5px;
    width: 100px;
    height: 100px;
    border: 1px solid $gray;
    background: $white;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    position: relative;
    box-shadow: 0px 12px 16px rgba(0, 0, 0, 0.03);

    @include md {
      width: 80px;
      height: 80px;
      margin: 12px; }
    @include sm {
      margin: 4px; } }

  &__link:hover {
    img {
      opacity: 0.6; } }

  &__img-box {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    @include xs {
      margin: 0 0 0; }

    img {
      transform: scale(0.80);
      max-height: 58px;
      display: block;
      color: #666;
      font-size: 16px;

      @include md {
        transform: scale(0.75); } } }

  &__title {
    font-size: 12px;
    text-align: center;
    display: none; } }
