.btn {
  display: flex;
  cursor: pointer;
  text-align: center;
  padding: 5px 40px;
  min-height: 56px;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 13px;
  @include bold;
  line-height: 1.3;
  color: $white;
  text-decoration: none;
  transition: all ease 0.3s;
  transform: translateZ(0) perspective(1px);
  backface-visibility: hidden;
  //transform-style: preserve-3d
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-backface-visibility: hidden;
  border: 2px solid transparent;
  border-radius: 8px;
  background: $themeColor;

  &:after {
    content: '';
    position: absolute;
    left: -2px;
    top: -2px;
    bottom: -2px;
    right: -2px;
    z-index: -2;
    background: linear-gradient(145deg, $themeColor, $themeColor);
    transition: all ease 0.1;
    border-radius: 8px;
    display: none; }

  &:before {
    transition: 0.2s;
    content: '';
    position: absolute;
    left: -2px;
    top: -2px;
    bottom: -2px;
    right: -2px;
    z-index: -1;
    background: linear-gradient(145deg, $themeColor, $themeColor);
    transition: all ease 0.1;
    border-radius: 8px;
    opacity: 0;
    display: none; }

  &:hover, &:focus-visible {
    background-color: $themeColorD;
    outline-width: 3px !important;
    &:before {
      opacity: 1; } }

  @include tablet {
    padding: 15px 20px; }

  &--md {
    min-height: 48px;
    font-size: 12px;
    padding-left: 30px;
    padding-right: 30px; }

  &--gradient {
    border: 2px solid transparent;
    color: #000;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -2;
      background: linear-gradient(145deg, $themeColor, $themeColor);
      transition: all ease 0.1; }

    &:before {
      transition: 0.2s;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
      background: linear-gradient(145deg, $themeColor, $themeColor);
      transition: all ease 0.1; }

    &:hover, &:focus-visible {
      &:before {
        opacity: 0; } }

    @include tablet {
      padding: 15px 20px; } }

  &--transparent {
    border-color: $gray2;
    background: transparent;
    color: $themeColor;

    &:after {}
    &:before {
      display: none; }

    &:hover, &:focus-visible {
      background: transparent;
      border: 2px solid $themeColor; } }

  &--secondary {
    border-color: #000;
    background: #000;
    color: #fff;

    &:hover, &:focus-visible {
      background: $themeColor;
      border-color: $themeColor; } }

  &--border {
    border: 1px solid rgba(255, 255, 255, 0.4);
    color: $white;
    position: relative;
    background: transparent;

    &:after {
      opacity: 0;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -2;
      border-radius: 25px;
      background: linear-gradient(to right, $themeColor, $themeColor);
      animation: AnimationName 5s linear infinite;
      display: none; }

    &:before {
      opacity: 0;
      transition: 0.2s;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
      border-radius: 25px;
      background: linear-gradient(to right, $themeColor, $themeColor);
      animation: AnimationName 5s linear infinite;
      display: none; }

    &:hover, &:focus-visible {
      color: $white;
      border-color: transparent;

      &:after {
        opacity: 1; } }

    @include tablet {
      padding: 15px 23px; } }

  &--huge {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 40px 34px 40px;
    font-weight: 900;
    line-height: 1.34;
    border: 60px solid $themeColor;
    border-width: 0 60px;
    font-stretch: 120%;
    color: $white;
    font-size: clamp(20px, 5vw, 24px);
    align-items: flex-end;
    justify-content: flex-end;
    background: $black;
    align-items: center;
    border-radius: 0;
    margin: clamp(20px, 4vw, 40px) auto;

    &:after,
    &:before {
      display: none; }

    @include tablet {
      padding: 15px 8vw; }

    @include sm {
      margin: 0;
      border-width: 0 40px; }

    .icon {
      fill: $white;
      width: 31px;
      height: 20px;
      margin-left: 16px; } } }

.icon-link {
  color: #fff;
  .icon {
    font-size: 18px;
    fill: $white; } }

.theme-link {
  font-size: 14px;
  color: $themeColor;
  text-transform: uppercase;
  text-decoration: none;
  &:hover {
    text-decoration: underline; } }

.is-hidden {
  display: none; }
