.s-features-grid {
  padding: 0 0 0;
  position: relative;
  z-index: 1;

  &:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    height: 150%;
    transform: translate(-50%, -50%);
    background: url(../img/features-bg.png) center center no-repeat;
    background-size: cover;
    z-index: -1; }

  .container {
    max-width: 1920px;
    padding: 0;
    height: 100%;
    width: 100%;
    position: relative; } }

.features-grid {
  justify-content: center;
  align-content: flex-start;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;

  &__content {
    position: absolute;
    left: -1px;
    top: -1px;
    right: -1px;
    bottom: -1px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity .3s ease;
    border-radius: 30px;
    overflow: hidden;
    .icon {
      fill: $white;
      font-size: 68px; } }

  &__item {
    margin: 0 0 -1px;
    min-height: calc((100vw / 4) - 30px);
    display: block;
    position: relative;
    text-decoration: none;
    border: 1px solid $gray;
    border-radius: 30px;
    @include hover {
      .features-grid__content {
        opacity: 1; }
      .features-grid__pop {
        opacity: 0; } }
    @include tablet {
      min-height: calc((100vw / 4) - 10px); }
    @include sm {
      min-height: calc(100vw / 2.05);
      border-radius: 16px; }
    .video {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 30px;
      @include sm {
        border-radius: 16px; } }

    &.quarter {
      width: calc(25% - 8px);
      @include sm {
        width: calc(50% - 8px); }

      @include xs {
        width: calc(100% - 8px); } }

    &.third {
      width: calc(33.3% - 8px);

      @include sm {
        width: calc(50% - 8px); }

      @include xs {
        width: calc(100% - 8px); } }

    &.half {
      width: calc(50% - 8px);

      @include sm {
        width: calc(50% - 8px); }

      @include xs {
        width: calc(100% - 8px); } }

    &.full {
      width: calc(100% - 8px); }

    &.no-link {
      @include hover {
        .features-grid__content {
          opacity: 1; }
        .features-grid__pop {
          opacity: 1; } } } }

  &__fb {
    display: block;
    width: 94px;
    height: 94px;
    background: url(../img/facebook-circle.svg) center center no-repeat; }

  &__img {
    position: absolute;
    left: -1px;
    top: -1px;
    right: -1px;
    bottom: -1px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
    transition: opacity .3s ease;
    border-radius: 30px;
    overflow: hidden;
    @include sm {
      border-radius: 16px; }

    &_hover {
      opacity: 0; } }


  &__pop {
    color: $white;
    font-size: 1.3vw;
    line-height: 1.5;
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 30px 32px;
    background: rgba(0, 0, 0, 0.5);
    transition: opacity .3s ease;
    opacity: 1;
    background: transparent;
    border-radius: 30px;
    overflow: hidden;

    @include md {
      font-size: 1.6vw;
      padding: 30px 30px; }

    @include sm {
      font-size: 3vw;
      padding: 20px 20px; } }

  &__title {
    font-weight: 900;
    font-size: clamp(24px, 3vw, 46px);
    font-stretch: 120%;
    line-height: 1;
    text-transform: uppercase;
    width: 100%; }

  &__description {
    font-size: clamp(12px, 1vw, 18px);
    line-height: 1.32;
    margin: 20px 0 0;
    max-width: 400px;
    width: 100%; }

  .btn {
    margin-top: 20px;
    background: $black;
    &:hover, &:focus-visible {
      background-color: $themeColor; } }

  img {
    height: 100%;
    width: 100%;
    object-fit: cover; } }
